import React from 'react'
import { useState, useEffect } from 'react';
import styled from 'styled-components';
//I did not label this file correct it handles countries and states

const Wrapper = styled.div`

`;

const ButtonBar = styled.div`
  padding-top:40px;
  display:flex;
  justify-content:center;
  gap:30px;
`;



const Title = styled.h2`
  
  font-size:20px;
  margin: 0;
`

const Select = styled.select`
  font-size:12px;
  padding: 3px;
  cursor: pointer;
  border-radius:5px;
  border: 2px solid #236f8a;
  background-color:white;
  font-weight:600px;
`;

const Option = styled.option`

`;


const Countries = (props) => {
    
    //loading was for originall to stop the user from interacting with the dropdown wile items are loading
    const [loading, setLoading] = useState(true);
    //countries states
    const [items, setItems] = useState([{ label: "Loading ...", value: "" }]);
    const [value, setValue] = useState("US");

    //cities states
    const [citems, setcItems] = useState([]);
    const [cvalue, setcValue] = useState("AMADOR");
    //setting intial value

    //using state to render dropdown items we need items to be dynamic and loaded from external web APi 
    /*
      So we need to put the items in a state. then when we have the dropdown refrence this state
    */ 

    useEffect(() => {

      //Its first parameter is a function to execute when the side effect runs

      //The second parameter determines when the side effect runs. In our case this is just after the component first renders because we have specified an empty array
      
      //Our side effect function in the useEffect hook needs to be asynchronous because of the web API call, but this isnt directly allowed in the useEffect. This is why we have an asynchronous nested getCharacters function that is called
      
      //Inside the getCharacters function we use the native fetch function to make the web API request. We then map the response body to the data structure that our items state expects
      let unmounted = false;
      // used unmounted because what if a user navigate to a different page and the drop down is unmounted while the items are still being fetched react wouldn't be happy when the response is returned and the stated is attempted to be set with set items. I resolved this with the unmounted flag
      async function getCharacters() {
        const response = await fetch(
          "https://api.openaq.org/v2/countries?limit=9999"
        );
        const body = await response.json();
        if (!unmounted) {
          setItems(
            body.results.map(({ name, code }) => ({ label: code, value: code,  val: name}))
          );
          setLoading(false);
        }

        const responsec = await fetch(
         
          "https://docs.openaq.org/v2/cities?limit=9999&page=1&offset=0&sort=asc&country_id="+ value[0]+value[1] +"&order_by=city"
            
          );
          
          const bodyc = await responsec.json();
          if (!unmounted) {
            setcItems(
              bodyc.results.map(({ city }) => ({ label: city, value: city }))
            );
            setLoading(false);
          }


      }
      getCharacters();
      return () => {
        unmounted = true;
      };
    }, [value[0]+value[1]]);

    const handleSubmit = () => {
      props.passCountryData(value);
      props.passCityData(cvalue);
    };
    //passing the data up to the parent
    return (
    <Wrapper>
      <ButtonBar>
        <Title>Countries:</Title>
          <Select on
            disabled={loading}
            value={value}
            onChange={(e) => setValue(e.currentTarget.value)}
            onClick={(e) => setcValue("Select A City")}
            
          >
            <option disabled selected> Select A Country</option>
            {items.map(({ label, value, val}) => (
              <option key={value} value={value} >
                {val + " - " + label}
              </option>
            ))}
          </Select>
          <Title>Cities:</Title>
          <Select
            menuPlacement="top"
            disabled={Object.keys(citems).length === 0}
            //setting this to be disabled if citems isEmpty
            value={cvalue}
            onChange={(e) => setcValue(e.currentTarget.value)}
            
          >
            <option disabled selected>Select A City</option>
            
            {citems.map(({ label, value }) => (
              <Option key={value} value={value}>
                {label}
              </Option>
            ))}
            
          </Select> 
          <button onClick={handleSubmit}>Submit</button>
        </ButtonBar>
    </Wrapper>        
    );


}

/*

    Used the useEffect hook to load drop down items from a web API. The side effect function needs to contain a nested function that does the web API call
    Used the useState hook for a loading flag that is set while drop down items are loading which can be used to disable the drop down during this process
    Used the useState hook to hold drop down items in state. This is set after the data has been fetched from the web API
    Also used the useState hook to control the selected drop down value in state. We can then set the initial selected value for the drop down by setting the initial value for the state


*/
  

export default Countries;

